import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior() {
    return { x: 0, y: 0 }
  },
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: () => import('@/views/Dashboard.vue'),
      beforeEnter: (to, from, next) => {
        if (!store.getters['auth/authenticated']){
         return next({
           name: 'login'
          })
        }
        next()
      },
    },
    {
      path: '/devicelist',
      name: 'devicelist',
      component: () => import('@/views/DeviceList.vue'),
      beforeEnter: (to, from, next) => {
        if (!store.getters['auth/authenticated']){
         return next({
           name: 'login'
          })
        }
        next()
      }
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/views/Login.vue'),
      beforeEnter: (to, from, next) => {
        if (store.getters['auth/authenticated']){
         return next({
           name: 'dashboard'
          })
        }
        next()
      },
      meta: {
        layout: 'full',
      },
    },
    {
      path: '/error-404',
      name: 'error-404',
      component: () => import('@/views/error/Error404.vue'),
      meta: {
        layout: 'full',
      },
    },
    // {
    //   path: '*',
    //   name: 'catchAll',
    //   component: () => import('@/views/Home.vue')
    // },
     {
      path: '*',
      redirect: 'error-404'
    },
    {   
        path:'/devicedetails/control',
        name: 'control',
        component: () => import('@/views/DeviceDetails/Control.vue'),
    },
    {   
      path:'/devicedetails/statistic',
        name: 'statistic',
        component: () => import('@/views/DeviceDetails/Statistic.vue'),
    },
    {
      path:'/devicedetails/timer',
        name: 'timer',
        component: () => import('@/views/DeviceDetails/Timer.vue'),
    },
  ],
})

export default router
